'use client';
import Link from 'next/link';
import { FaBars } from 'react-icons/fa';

type NavbarProps = {
  onHamburgerClick: () => void;
};

const Navbar: React.FC<NavbarProps> = ({ onHamburgerClick }) => {
  return (
    <nav className="navbar">
      <div className="hamburger" onClick={onHamburgerClick}>
        <FaBars size={24} />
      </div>

      <div className="logo">
        <Link href="/">
          <img src="/logo.svg" alt="Logo" className="logo-img" />
        </Link>
      </div>

      <div className="contacts">
        <Link href="/contacts">Контакты</Link>
      </div>

      <style jsx>{`
        .navbar {
          display: flex;
          justify-content: space-between;
          align-items: center;
          background-color: transparent;
          color: black;
          position: relative;
          width: 100%;
          padding-left: 10%;
          padding-right: 10%;
          padding-top: 2%;
          z-index: 1000;
        }

        .hamburger {
          display: block;
          cursor: pointer;
        }

        .logo {
          display: flex;
          justify-content: center;
          align-items: center;
          flex: 1;
        }

        .logo-img {
          width: 160px;
          height: auto;
        }

        .contacts a {
          color: black;
          font-size: 18px;
          text-decoration: none;
        }
      `}</style>
    </nav>
  );
};

export default Navbar;
